.task-list{width:50%;margin:0 auto;color:black}.task-content{margin:20px 0;padding:20px;width:100%;display:flex;justify-content:space-between;background-color:whitesmoke}.task-hours{font-size:24px;margin:15px}.task-right-container{align-self:center}.delete-button{font-size:20px;z-index:10;cursor:pointer;border:none;background-color:transparent}.loading{opacity:0;display:flex;position:fixed;bottom:50px;left:50%;transform:translateX(-50%);transition:opacity 0.3s ease-in}.loading.show{opacity:1}.ball{background-color:#777;border-radius:50%;margin:5px;height:10px;width:10px;-webkit-animation:jump 0.5s ease-in infinite;animation:jump 0.5s ease-in infinite}.ball:nth-of-type(2){-webkit-animation-delay:0.1s;animation-delay:0.1s}.ball:nth-of-type(3){-webkit-animation-delay:0.2s;animation-delay:0.2s}@-webkit-keyframes jump{0%,100%{transform:translateY(0)}50%{transform:translateY(-10px)}}@keyframes jump{0%,100%{transform:translateY(0)}50%{transform:translateY(-10px)}}
